  //     context.drawImage(video, 0, 0, canvasWidth, canvasHeight);
  //     const videoData = context.getImageData(0, 0, canvasWidth, canvasHeight);
  //     const len = videoData.data.length;

  //     for (let i = 0; i < len; i++) {
  //       videoData.data[i] /= 4;
  //     }

  //     context.putImageData(videoData, 0, 0);

  //     // canvas.style.display = "none";
